/* Vars 
------------------*/
@pathAssets: "./assets";
@fa-font-path: "@{pathAssets}/icon/webfonts";
@pathFont: "@{pathAssets}/fonts";

@mainColor: #202225;
@secondColor: #fff;

@buttonColorPrim: #ed4245;
@linkColorPrim: #3484e5;

@donatorColor: #1d8ae2;

@max-width: 2500px;
@max-width-content: 1500px;
@blinkColor: #38a517;

/* External stylesheet 
------------------*/
//@import "@{pathFont}/icon/all.css";



/* Core functions 
------------------*/
.gradientLinear(@background, @deg, @color1, @color2) {
	background: @background -moz-linear-gradient(@deg, @color1, @color2);
	background: @background -webkit-linear-gradient(@deg, @color1, @color2);
	background: @background linear-gradient(@deg, @color1, @color2);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="@color1",endColorstr="@color2",GradientType=1);
}

.gradientLinearImg(@background, @deg, @color1, @color2, @url) {
    background-color: @background;
    background-image: url(@url), -moz-linear-gradient(@deg, @color1, @color2);
    background-image: url(@url), -webkit-linear-gradient(@deg, @color1, @color2);
    background-image: url(@url), linear-gradient(@deg, @color1, @color2);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="@color1",endColorstr="@color2",GradientType=1);
}

.box-shadow(@shadow, @inset:false) {
    & when (@inset = true) {
        -webkit-box-shadow:inset @shadow;
        box-shadow:inset @shadow;
    }
    & when (@inset = false) {
        -webkit-box-shadow: @shadow;
        box-shadow: @shadow;
    }
}

.box-sizing(@param) {
    -webkit-box-sizing: @param;
    box-sizing: @param;
}

.borderradius(@degree) {
	-webkit-border-radius: @degree;
	border-radius: @degree;
}

.backblur(@blur) {
    -webkit-backdrop-filter: blur(@blur);
    backdrop-filter: blur(@blur);
}

.transition(@type, @time, @easing) {
	-webkit-transition: @type @time @easing;
	-moz-transition: @type @time @easing;
	-ms-transition: @type @time @easing;
	-o-transition: @type @time @easing;
	transition: @type @time @easing;
}

.transform(@type) {
    -moz-transform: @type;
    -webkit-transform: @type;
    -o-transform: @type;
    -ms-transform: @type;
    transform: @type;
}

.cover {
	-webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}

.shadow-gloss {
	-moz-box-shadow: rgba(0,0,0,0.1) 0px 1px 3px, inset rgba(255,255,255,0.2) 0px 1px 0px;
	-webkit-box-shadow: rgba(0,0,0,0.1) 0px 1px 3px, inset rgba(255,255,255,0.2) 0px 1px 0px;
	box-shadow: rgba(0,0,0,0.1) 0px 1px 3px, inset rgba(255,255,255,0.2) 0px 1px 0px;
}

.buttonCSS {
    width: 100%;
    text-decoration: center;
    padding: 10px;
    margin: 20px 0;
    background: fade(@buttonColorPrim, 20%);
    border: 1px solid lighten(@buttonColorPrim, 10);
    color: @secondColor;
    cursor: pointer;
    .backblur(4px);
    .transition(background, 300ms, ease-in-out);
    .borderradius(3px);
    &:hover {
        background: fade(@buttonColorPrim, 50%);
    }
}