/* Global var and function 
------------------*/
@import (reference) "./variable.less";
@import "@{pathFont}/icon/fontawesome.less";
@import "@{pathFont}/icon/solid.less";
@import "@{pathFont}/icon/brands.less";

/* Fonts 
------------------*/
@font-face{
	font-family: sf display;
	src:url('@{pathFont}/sf display/sf display.woff') format('woff');
    font-display: swap;
}

@font-face{
	font-family: Montserrat;
	src:url('@{pathFont}/montserrat/Montserrat-Bold.woff') format('woff');
    font-display: swap;
}

@font-face{
	font-family: Poppins;
	src:url('@{pathFont}/poppins/poppins-v15-latin-regular.woff') format('woff'),
    url('@{pathFont}/poppins/poppins-v15-latin-regular.woff2') format('woff2');
    font-display: swap;
}


/* Animation
------------------*/
@keyframes blink {
    0%,
    100%,
    24.9%,
    75.1% {
        opacity: 0;
    }
    25%,
    75% {
        opacity: 1;
    }
}

@keyframes showModal {
    0% {
        left: -99999px;
    }
    25%,
    100% {
        left: 0;
    }
}

@keyframes showAlert {
    0% {
        bottom: -99999px;
    }
    50% {
        bottom: 10px;
        .transform(scale(0.5));
    }
    65% {
        .transform(scale(1));
    }
    70%, 80%, 90%, 100% {
        left: 2%;
    }
    75%, 85%, 95% {
        left: 3%;
    }
}

@keyframes sweep {
    0% {
        background-position: right;
    }
    100% {
        background-position: left;
    }
}

@keyframes characterRegister {
    0% {
        top: -999px;
        opacity: 0.5;
    }
    100% {
        top: 0;
        opacity: 1;
    }
}

/* Global 
------------------*/
* {
    margin: 0;
    padding: 0;
    .box-sizing(border-box);
    li {
        list-style: none;
    }
    a {
        color: @linkColorPrim;
    }
    &::-webkit-scrollbar-track {
        background-color: lighten(@mainColor, 20);
        .borderradius(10px);
        .box-shadow(0 0 6px rgba(0,0,0,0.3), true);
    }
    &::-webkit-scrollbar {
        width: 8px;
        background-color: invisibel;
    }
    &::-webkit-scrollbar-thumb {
        background-color: lighten(@mainColor, 5);
        .borderradius(10px);
        .box-shadow(0 0 6px rgba(0,0,0,0.3), true);
    }
}

.clr:after {
    content:"";
    display:block;
    clear:both;
}

body {
    padding: 0;
    margin: 0;
    background: @mainColor;
    color: @secondColor;
    font: 16px "sf display","Helvetica Neue",Helvetica,Arial,sans-serif;
}

.wrapper {
    position: relative;
    margin: 0 auto;
    max-width: @max-width;
}

._displayFlex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
nav {
    ul {
        li {
            display: inline-table;
            a {
                text-decoration: none;
            }
        }
    }
}

.engine-wrapper {
    position: absolute;
    top: 0;
    left : 0;
    width: 100%;
    height: 100%;
    canvas {
        width: 100% !important;
        height: 100% !important;
        &.registerCanvas {
            height: auto !important;
        }
    }
}

.toolip {
    position: relative;
    ._Toolip {
        position: relative;
        .showToolip {
            display: none;
            position: absolute;
            z-index: 2;
            top: 80px;
            left: -40px;
            width: 120px;
            padding: 5px;
            text-align: center;
            color: @mainColor;
            font-family: Montserrat;
            font-size: 10px;
            background: @secondColor;
            .borderradius(4px);
            .secondToolip {
                color: lighten(@mainColor, 40);
            }
            &::after{
                z-index: -10;
                content: ' ';
                position: absolute;
                top: -5px;
                left: calc(50% - 5px);
                border: 5px solid @secondColor;
                border-color: @secondColor transparent transparent @secondColor;
                .transform(rotate(45deg));
            }
        }
    }
    ._ToolipBind:hover + .showToolip {
        display: block;
        top: 50px;
    }
}

.tablist {
    .tablistWindow {
        &:not(.actived) {
            display: none;
        }
    }
    .tablistButton {
        li.button {
            display: inline-block;
            cursor: pointer;
            color: lighten(@mainColor, 40);
            background: fade(lighten(@mainColor, 20), 20);
            border: 1px solid fade(lighten(@mainColor, 40), 2);
            padding: 20px 30px;
            .transition(background, 300ms, ease-in-out);
            &.active {
                background: fade(lighten(@mainColor, 60), 20);
                .box-shadow(0 0 10px 0 rgba(0,0,0,0.2), true);
            }
            &:hover {
                background: fade(lighten(@mainColor, 50), 20);
            }
        }
    }
}

.alert {
    @colorBaseAlert: lighten(@mainColor, 10);
    position: fixed;
    bottom: 10px;
    left: 2%;
    z-index: 5;
    width: 95%;
    padding: 15px;
    text-align: center;
    background: @colorBaseAlert;
    border: 1px solid lighten(@colorBaseAlert, 15);
    animation: 2s showAlert ease-in-out;
    &.success {
        span {
            @colorBaseAlert: #378e26;
            background: fade(@colorBaseAlert, 70);
            border: 1px solid lighten(@colorBaseAlert, 20);
        }
    }
    &.danger {
        span {
            @colorBaseAlert: #ba1818;
            background: fade(@colorBaseAlert, 70);
            border: 1px solid lighten(@colorBaseAlert, 20);
        }
    }
    &.info {
        span {
            @colorBaseAlert: #c9c00e;
            background: fade(@colorBaseAlert, 70);
            border: 1px solid lighten(@colorBaseAlert, 20);
        }
    }
    &.warning {
        span {
            @colorBaseAlert: #ba7c18;
            background: fade(@colorBaseAlert, 70);
            border: 1px solid lighten(@colorBaseAlert, 20);
        }
    }
    span {
        padding: 5px 25%;
    }
    a.close {
        float: right;
        margin-left: 20px;
        font-size: 18px;
        cursor: pointer;
        color: @secondColor;
    }
}

.packIcon {
    width: 30px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    text-align: center;
    vertical-align: center;
    background: fade(@mainColor, 90);
    .backblur(10px);
    h3 {
        text-transform: uppercase;
        font-family: Montserrat;
    }
    &.big {
        animation: 5s showModal;
        padding: 20% 100px;
        width: 100%;
        height: 100vh;
    }
    button {
        .buttonCSS
    }
}

.skeleton {
    background-image: linear-gradient( 90deg, fade(lighten(@mainColor, 50), 20), fade(lighten(@mainColor, 60), 20), fade(lighten(@mainColor, 40), 20), fade(lighten(@mainColor, 30), 20)) !important;
    background-size: 1000%;
    background-position: right;
    animation: sweep 1s ease-in-out infinite alternate;
    &.--text {
        display: inline-block;
        width: 100px;
        height: 15px;
    }
    &.--inline {
        display: inline-block;
    }
    &.--darker {
        background-image: linear-gradient( 90deg, fade(lighten(@mainColor, 20), 20), fade(lighten(@mainColor, 30), 20), fade(lighten(@mainColor, 10), 20), fade(@mainColor, 20)) !important;        
    }
    &.--radius {
        .borderradius(4px);
    }
}

.avatarSvg {
    svg {
        position: relative;
        top: -5px;
        width: 100%;
        height: 100%;
    }
}

/* Header 
------------------*/
header {
    position: absolute;
    z-index: 4;
    width: 100%;
    nav {
        &.main {
            ul.navigation {
                display: inline;
                padding-left: 30px;
                li.itemNav {
                    margin: 0 10px;
                    a.navLink {
                        display: inline-block;
                        padding-top: 5px;
                        color: darken(@secondColor, 10);
                        font-weight: bold;
                        font-family: Poppins;
                        .transition(all, 300ms, ease-in-out);
                        &:hover:not(nav.main ul li.active a, nav.main ul li.user a) {
                            color: lighten(@mainColor, 30);
                            border-top: 30px solid darken(@secondColor, 10);
                        }
                    }
                    &.active {
                        border-top: 30px solid darken(@secondColor, 10);
                    }
                    &.user {
                        position: relative;
                        float: right;
                        margin-top: 25px;
                        margin-right: 20px;
                        text-transform: uppercase;
                        a.navLink {
                            padding: 5px 30px;
                            border: 2px solid darken(@secondColor, 10);
                            .transition(all, 300ms, ease-in-out);
                            .borderradius(20px);
                            &:hover {
                                background: darken(@secondColor, 10);
                                color: lighten(@mainColor, 30);
                            }
                        }
                        &.logged {
                            display: flex;
                            align-items: center;
                            text-transform: none;
                            a {
                                border:  none;
                            }
                            .avatar{
                                width: 55px;
                                margin-right: 5px;
                                cursor: pointer;
                                .borderradius(50%);
                                svg {
                                    .borderradius(50%);
                                }
                                &:hover {
                                    opacity: 0.7;
                                }
                            }
                        }
                        div.navDropdown {
                            display:  grid;
                            @DrownDownColor: lighten(@mainColor, 10);
                            grid-template-columns: repeat(5, 1fr);
                            grid-template-rows: repeat(1, 1fr);
                            position: absolute;
                            top: 70px;
                            left: -160px;
                            width: 400px;
                            background: @DrownDownColor;
                            color: @mainColor;
                            .borderradius(4px);
                            ul.userNav {
                                position: relative;
                                z-index: 2;
                                &.stats {
                                    grid-area: 1 e("/") 1 e("/") 2 e("/") 4;
                                    padding: 5px;
                                    border-right: 1px solid lighten(@DrownDownColor, 20);
                                    li.navShowStats {
                                        display: block;
                                        padding: 5px;
                                        color: lighten(@DrownDownColor, 60);
                                        &:not(&:last-child) {
                                            border-bottom: 1px solid lighten(@DrownDownColor, 5);
                                        }
                                        ul.navList {
                                            li {

                                            }
                                        }
                                    }
                                }
                                &.navigate {
                                    grid-area: 1 e("/") 4 e("/") 2 e("/") 6;
                                    height: 200px;
                                    li {
                                        display: block;
                                        a {
                                            display: block;
                                            padding: 24px 5px;
                                            background: lighten(@DrownDownColor, 20);
                                            color: lighten(@DrownDownColor, 50);
                                            .box-shadow(-12px -8px 40px rgba(70, 70, 70, 0.7), true);
                                            .transition(all, 300ms, ease-in-out);
                                            &:hover {
                                                background: lighten(@DrownDownColor, 50);
                                                color: lighten(@DrownDownColor, 20);
                                            }
                                            &:first-child {
                                                .borderradius(0 4px 0 0);
                                            }
                                        }
                                    }
                                }
                                &.characters {
                                    grid-area: 2 e("/") 1 e("/") 3 e("/") 6;
                                    li.nav_characterBox {
                                        position: relative;
                                        display: block;
                                        background-color: lighten(@DrownDownColor, 30);
                                        background-position: 30px -20px;
                                        .box-shadow(-12px -8px 40px rgba(70, 70, 70, 0.7), true);
                                        ul.nav_characterStats {
                                            padding: 5px;
                                            width: 90%;
                                            li {
                                                margin: 10px;
                                                span {
                                                    display: block;
                                                    text-transform: uppercase;
                                                    font-size: 15px;
                                                }
                                                b {
                                                    font-size: 13px;
                                                    color: lighten(@DrownDownColor, 50);
                                                    i {
                                                        color: #42d13a;
                                                    }
                                                }
                                                &.showProgress {
                                                    width: 100%;
                                                    margin: 5px;
                                                    .progressLevel {
                                                        width: 100%;
                                                        height: 10px;
                                                        background: lighten(@DrownDownColor, 5);
                                                        .borderradius(5px);
                                                        &.--progressed {
                                                            background: lighten(@DrownDownColor, 10);
                                                        }
                                                    }
                                                }
                                            }
                                            h5 {
                                                padding-left: 10px;
                                                font-size: 23px;
                                                color: lighten(@DrownDownColor, 60);
                                            }
                                            a.gestCharaLink {
                                                position: absolute;
                                                top: 10px;
                                                right: 10px;
                                                text-decoration: none;
                                                padding: 5px;  
                                                background: fade(@buttonColorPrim, 20%);
                                                border: 1px solid lighten(@buttonColorPrim, 10);
                                                color: @secondColor;
                                                .backblur(4px);
                                                .transition(background, 300ms, ease-in-out);
                                                .borderradius(3px);
                                                &:hover {
                                                    background: fade(@buttonColorPrim, 50%);
                                                }
                                            }
                                        }
                                        &:last-child {
                                            .borderradius(0 0 4px 4px);
                                        }
                                    }
                                }
                                h4 {
                                    font-family: Poppins;
                                    text-transform: uppercase;
                                    color: lighten(@DrownDownColor, 40);
                                }
                            }
                            .--dropFlex {
                                display: flex;
                                justify-content: space-between;
                                align-items: center;
                            }
                            &::before {
                                content: '';
                                position: absolute;
                                top: -3px;
                                left: 178px;
                                background: @DrownDownColor;
                                width: 20px;
                                height: 20px;
                                .transform(rotate(45deg));
                            }
                        }
                    }
                }
            }
        }
    }
    img.logo {
        float: left;
        margin: 10px;
        width: 250px;
        height: 70px;
        cursor: pointer;
    }
}

/* Footer 
------------------*/
footer {
    display: flex;
    align-items: center;
    background-image: url('@{pathAssets}/img/design/pers_footer.png');
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 400px;
    height: 300px;
    padding: 0 10px;
    ul {
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        li {
            &:first-child {
                width: 50%;
            }
            h5 {
                font-size: 20px;
                text-transform: uppercase;
                font-family: Montserrat;
            }
            ol {
                li {
                    a {
                        display: inline-block;
                        text-decoration: none;
                        border-left: 2px solid lighten(@mainColor, 10);
                        padding-left: 10px;
                        margin-bottom: 5px;
                        &:hover {
                            color: @buttonColorPrim;
                        }
                    }
                }
            }
            span {
                &.copyright {
                    display: inline-block;
                    margin-top: 20px;
                    font-size: 13px;
                }
            }
            img {
                width: 200px;
            }
        }
    }
}

/* Sections
------------------*/
section {
    position: relative;
    width: 100%;
    &.home {
        height: 100vh;
        &.main {
            background-image: url('@{pathAssets}/img/black/sections/home/header.png');
            background-repeat: no-repeat;
            background-position: bottom;
            background-size: cover;
            .top_right {
                display: block;
                height: 100%;
                background-image: url('@{pathAssets}/img/design/header_right1.png');
                background-repeat: no-repeat;
                background-position: right;
            }
            .top_social {
                position: absolute;
                top: 35%;
                left: 1%;
                ul {
                    li {
                        margin: 35px 0;
                        background: darken(@secondColor, 10);
                        text-align: center;
                        .borderradius(4px);
                        .transition(all, 300ms, ease-in-out);
                        a {
                            display: inline-block;
                            color: lighten(@mainColor, 10);
                            padding: 7px;
                            font-size: 25px;
                            text-decoration: none;
                        }
                        &:hover {
                            background: lighten(@mainColor, 10);
                            a {
                                color: @secondColor;
                            }
                        }
                    }
                }
            }
            .top_pin {
                position: absolute;
                top: 30%;
                left: 7%;
                width: 50%;
                h1 {
                    font-size: 30px;
                    color: @secondColor;
                    text-transform: uppercase;
                    span {
                        font-size: 40px;
                    }
                }
                p {
                    width: 80%;
                    color: darken(@secondColor, 10);
                    font-size: 12px;
                    margin-bottom: 35px;
                }
                a.register {
                    position: relative;
                    margin: 20px 0;
                    color: @mainColor;
                    background: darken(@secondColor, 10);
                    padding: 10px 40px;
                    font-size: 20px;
                    font-family: Poppins;
                    text-decoration: none;
                    text-transform: uppercase;
                    .transition(all, 300ms, ease-in-out);
                    .borderradius(30px);
                    .shadow-gloss();
                    i {
                        position: absolute;
                        top: 15px;
                        left: 30px;
                        display: none;
                    }
                    &:hover {
                        background: lighten(@mainColor, 30);
                        color: darken(@secondColor, 10);
                        padding: 10px 40px 10px 60px;
                        i {
                            display: inline-block;
                        }
                    }
                }
                div.server {
                    position: relative;
                    font-family: Poppins;
                    padding: 20px;
                    margin-top: 80px;
                    width: 50%;
                    .backblur(10px);
                    .shadow-gloss();
                    h2 {
                        color: lighten(@mainColor, 40);
                    }
                    span {
                        display: block;
                        &.ip {
                            margin-bottom: 10px;
                            color: darken(@secondColor, 10);
                        }
                        &.player {
                            text-align: right;
                            margin-top: 20px;
                            color: darken(@secondColor, 10);
                            b {
                                color: darken(@secondColor, 30);
                                font-size: 25px;
                            }
                        }
                    }
                    div.blink {
                        position: absolute;
                        top: 10px;
                        right: 10px;
                        background: @blinkColor;
                        animation: 1s infinite blink; 
                        width: 15px;
                        height: 15px;
                        .box-shadow(0 0 15px 0 @blinkColor);
                        .borderradius(50%);
                    }
                    .progress {
                        width: 100%;
                        height: 10px;
                        background: lighten(@mainColor, 20);
                        .borderradius(30px);
                        &.-progressed {
                            background: lighten(@mainColor, 40);
                        }
                    }
                    .connected {
                        margin-top: -40px;
                    }
                }
            }
        }
        &.present {
            overflow: hidden;
            background-repeat: no-repeat;
            background-position: bottom;
            background-size: cover;
            h1 {
                position: relative;
                z-index: 2;
                top: 30px;
                margin: 20px;
                height: 0;
                font-size: 40px;
                font-family: Montserrat;
                text-transform: uppercase;
                span {
                    width: 60%;
                    position: relative;
                    top: -7px;
                    right: -20px;
                    display: block;
                    font-size: 30px;
                    color: darken(@secondColor, 20);
                }
                &.first {
                    height: 100px;
                }
            }
            &.announce-wrap {
                height: 140vh;
                background: lighten(@mainColor, 30);
            }
            &:nth-child(3) {
                display: flex;
                background-image: url('@{pathAssets}/img/design/home/bg-present.png');
            }
            .viewUs {
                display: flex;
                width: 75%;
                text-align: left;
                float: left;
                .view_exemple {
                    position: relative;
                    left: -20px;
                    z-index: 2;
                    width: 5%;
                    height: 500px;
                    background-color: lighten(@mainColor, 10);
                    overflow: hidden;
                    opacity: 0.5;
                    .transform(skewX(-15deg));
                    .borderradius(4px);
                    .transition(width, 300ms, ease-in-out);
                    &:first-child {
                        width: 80%;
                    }
                    &:hover {
                        width: 80%;
                    }
                    img {
                        position: relative;
                        left: -80px;
                        width: 1200px;
                        .transform(skewX(15deg));
                    }
                }
            }
        }
        .homeContent {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            max-width: @max-width-content;
            margin: 0 auto;
            img.skinLeft {
                width: 600px;
                &.right {

                }
            }
            &.first {
                background-image: url('@{pathAssets}/img/design/home/pers4.png');
                background-repeat: no-repeat;
                background-position: -150px bottom;
                background-size: 550px;
                height: 85%;
            }
            .download {
                display: inline-block;
                position: relative;
                z-index: 2;
                height: 390px;
                &.text {
                    display: flex;
                    align-items: center;
                    font-family: Poppins;
                    font-size: 20px;
                    padding: 5px;
                    margin: 0 10px;
                    color: fade(darken(@secondColor, 10), 50);
                }
                &.link {
                    padding-right: 10px;
                    ul {
                        display: flex;
                        align-items: center;
                        text-align: center;
                        height: 100%;
                        a {
                            position: relative;
                            top: 0;
                            display: inline-block;
                            width: 250px;
                            height: 100%;
                            text-transform: uppercase;
                            text-decoration: none;
                            color: @secondColor;
                            font-family: Montserrat;
                            .transition(top, 300ms, ease-in-out);
                            li {
                                display: flex;
                                flex-direction: column;
                                justify-content: space-between;
                                align-items: baseline;
                                width: 100%;
                                height: 100%;
                                background-repeat: no-repeat;
                                background-position: right;
                                background-size: 250px;
                                .shadow-gloss();
                                .borderradius(4px);
                                h5 {
                                    font-size: 27px;
                                }
                                span {
                                    display: inline-block;
                                    font-size: 40px;
                                    margin-left: 20px;
                                    opacity: 0.8;
                                }
                                p {
                                    font-size: 12px;
                                }
                            }
                            &:hover {
                                top: -30px;
                            }
                            &:nth-child(1) {
                                li {
                                    background-image: url('@{pathAssets}/img/design/home/download_1.png');
                                }
                            }
                            &:nth-child(3) {
                                li {
                                    background-image: url('@{pathAssets}/img/design/home/download_2.png');
                                }
                            }
                            &:nth-child(5) {
                                li {
                                    background-image: url('@{pathAssets}/img/design/home/download_3.png');
                                }
                            }
                        }
                        i {
                            font-size: 50px;
                            margin: 0 30px;
                            opacity: 0.3;
                        }
                    }
                }
            }
            .announce {
                position: relative;
                z-index: 3;
                width: 100%;
                ul {
                    li {
                        overflow: hidden;
                        background-image: var(--announce-background);
                        background-repeat: no-repeat;
                        background-size: cover;
                        cursor: pointer;
                    }
                    &.lastes {
                        display: grid;
                        grid-template-columns: repeat(4, 1fr);
                        grid-template-rows: repeat(7, 1fr);
                        grid-gap: 5px;
                        height: 400px;
                        li {
                            &:first-child {
                                grid-column: 1 e("/") 3;
                                grid-row: 1 e("/") 8;
                            }
                            &:nth-child(2) {
                                grid-column: 3 e("/") 5;
                                grid-row: 1 e("/") 5;
                            }
                            &:nth-child(3) {
                                grid-column: 3 e("/") 5;
                                grid-row: 5 e("/") 8;
                            }
                        }
                    }
                    &.all {
                        display: flex;
                        flex-flow: row wrap;
                        li {
                            flex: auto;
                            height: 300px;
                            margin: 5px 0;
                            &:nth-child(2) {
                                margin: 5px 5px;
                            }
                        }
                    }
                }
            }
        }
        .svgBackground {
            position: absolute;
            z-index: 2;
            left: 0px;
            top: 0px;
            path {
                fill: lighten(@mainColor, 5);
            }
            &.second {
                path {
                    fill: @mainColor;
                }
            }
            &.bottom {
                top: auto;
                bottom: -1px;
                .transform(rotate(180deg));
                path {
                    fill: lighten(@mainColor, 30);
                }
                &.last {
                    path {
                        fill: @mainColor;
                    }
                }
            }
        }
    }
}

/* Form container
------------------*/
.card {
    position: relative;
    background-color: lighten(@mainColor, 10);
    background-image: url('@{pathAssets}/img/black/sections/auth/card.png');
    background-position: top -300px;
    width: 40%;
    margin: 0 auto;
    padding: 20px;
    .borderradius(3px);
    .cover();
    .card-header {
        font-family: Poppins;
        font-weight: bold;
        font-size: 25px;
        margin-bottom: 30px;
        span {
            position: relative;
            top: -10px;
            display: block;
            font-size: 15px;
            color: darken(@secondColor, 10);
        }
    }
    &.character {
        margin: 0;
        animation: 1s characterRegister ease-in-out;
        .card-header {
            margin-bottom: 0;
        }
        .form-group {
             margin-bottom: 5px;
            &.button {
                position: absolute;
                top: 15px;
                right: 20px;
            }
        }
    }
    a.linkPass_rec {
        text-decoration: none;
    }
    .check-register {
        font-size: 17px;
        text-transform: uppercase;
        a {
            color: @buttonColorPrim;
            text-decoration: none;
            &:hover {
                color: lighten(@buttonColorPrim, 20);
            }
        }
    }
    &.registerCard {
        overflow: hidden;
    }
}

/* Form module
------------------*/
form {
    .form-group {
        margin-bottom: 30px;
        .displayMultiple {
            display: flex;
            .main {
                width: 100%;
            }
            &.genderWidth {
                justify-content: space-between;
            }
            .itemMultiple:not(:last-child) {
                margin-right: 15px;
            }
        }
        &.profile-pic {
            display: flex;
            align-items: center;
            .suggest-avatar {
                width: 50%;
                text-align: center;
                span {
                    display: block;
                    color: lighten(@buttonColorPrim, 20);
                    font-size: 12px;
                }
                svg {
                    width: 100px;
                }
            }
        }
        label {
            width: 100%;
            text-transform: uppercase;
            font-size: 12px;
        }
        p {
            margin-top: 5px;
            font-size: 12px;
            color: darken(@secondColor, 20);
        }
        .invalid-feedback {
            padding-top: 10px;
            ul {
                padding-left: 20px;
                li {
                    list-style: square;
                    text-transform: uppercase;
                    color: lighten(@buttonColorPrim, 5);
                    font-size: 13px;
                }
            }
        }
        .circleChoose {
            display: inline-block;
            margin-top: 10px;
            margin-right: 20px;
            cursor: pointer;
            &.skinChoose {
                width: 30px;
                height: 30px;
                border: 1px solid lighten(@mainColor, 50);
                .borderradius(50%);
                .transition(opacity, 300ms, ease-in-out);
                &:hover {
                    opacity: 0.5;
                }
                &.white {
                    background: #fde5c9;
                }
                &.black {
                    background: #b27b52;
                }
                &.active {
                    border: 3px solid @buttonColorPrim;
                }
            }
            &.sexChoose {
                text-align: center;
                background: darken(@secondColor, 10);
                width: 100%;
                padding: 5px;
                color: @mainColor;
                .borderradius(4px);
                .transition(background, 300ms, ease-in-out);
                &:hover {
                    background: darken(@secondColor, 30);
                }
                .circleText {
                    position: relative;
                    top: -4px;
                    font-weight: bold;
                    font-family: poppins;
                    text-transform: uppercase;
                }
                i {
                    font-size: 30px;
                    margin-right: 10px;
                }
                &.active {
                    background: @buttonColorPrim;
                    .box-shadow(0 0 15px rgba(0,0,0,0.5), true);
                }
            }
        }
    }
    .label-file {
        cursor: pointer;
        color: @linkColorPrim;
        font-weight: bold;
        margin-left: 20px;
    }
    .label-file:hover {
        color: darken(@linkColorPrim, 20);
    }
    .input-file {
        display: none;
    }
    select {
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        cursor: pointer;
        color: darken(@secondColor, 10);
        width: 100%;
        padding: 10px;
        border: 1px solid @mainColor;
        background-color: lighten(@mainColor, 20);
        background-image: url(data:image/svg+xml;base64,);
        background-size: .8em;
        background-repeat: no-repeat;
        background-position: calc(100% - .4em) center;
        .borderradius(4px);
    }
    textarea {
        width: 100%;
        height: 100px;
        background: lighten(@mainColor, 20);
        color: darken(@secondColor, 10);
        border: 1px solid @mainColor;
    }
    input[type="text"], input[type="password"] {
        width: 100%;
        background: lighten(@mainColor, 20);
        color: darken(@secondColor, 10);
        border: 1px solid @mainColor;
        padding: 10px;
        .borderradius(4px);
        &:focus {
            outline: none;
        }
        &::placeholder {
            font-size: 12px;
            padding-left: 5px;
        }
    }
    input[type="text"], input[type="password"], textarea {
        &.is-invalid {
            border: 1px solid lighten(@buttonColorPrim, 5);
        }
    }
    button {
        .buttonCSS();
    }
}
